.nav-flex-container{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 32px 0;
  max-width: 1152px;
  margin: auto;
  gap: 20px;
}

.nav-logo{
  font-size: 1.5rem;
  font-weight: bold;
  color: black;
}

.nav-search-input{
  background: rgba(255, 255, 255,0.75);
  padding: 0.75rem 2rem;
  border-radius: 9999px;
  border: none;
  outline: none;
  width: 280px;
  box-shadow: 2px 6px 4px 2px rgba(254, 202, 202, 0.25);
  transition: box-shadow 0.3s;
}

.nav-search-input:focus{
  box-shadow: 2px 6px 4px 2px rgba(254, 202, 202, 0.65);
}

.nav-link-li{
  color: black;
  transition: color 0.3s;
}

.nav-link-li:hover{
  color: aquamarine;
}

.nav-link-ul{
  display: flex;
  gap: 1.25rem;  /* flex안에있는 태그들에 갭을 준다*/
}

@media(min-width:1024px){
  .nav-flex-container
  {
    gap:0;
  }
  .nav-search-input{
    width: 24rem;
  }
}